body {
  font-family: Arial, sans-serif;
}

.container {
  margin-left:10em;
    margin-top:10em;
    margin-right:10em;
    margin-bottom:10em;
}

h1 {
  color: #333;
}

input {
  padding: 1em;
  margin: 1em 0;
  border: 1px solid #ccc;
  border-radius: 0.3em;
  width: 30em;
}

textarea {
  padding: 1em;
  margin: 1em 0;
  border: 1px solid #ccc;
  border-radius: 0.3em;
  width: 100em;
  font-family: Arial, sans-serif;
}

button {
  background: #007bff;
  color: white;
  border: none;
  padding: 10px 15px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

button:hover {
  background: #0056b3;
}

pre {
  background: #eee;
  padding: 10px;
  border-radius: 5px;
  text-align: left;
}

.spinner-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.spinner {
  width: 1em;
  height: 1em;
  border: 0.1em solid #ccc;
  border-top: 0.1em solid #505050;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.icon:hover {
  color: grey;
  cursor: pointer;  
}

.click {
  color: grey;  
}

.click:hover {
  cursor: pointer;  
}

.small-container {
  display: grid;
  grid-template-columns: auto 1fr; /* Icon takes auto width, text takes remaining space */
  align-items: center; /* Aligns icon and text vertically */
  gap: 1em;
}

@media only screen and (max-width : 600px) {
  .container {
    margin-left:2em;
    margin-top:2em;
    margin-right:2em;
    margin-bottom:2em;
  }
}



.iconsearch:hover{
  background-color: rgb(199, 199, 199);
  cursor: pointer;
}

.context{
  padding: 0.5em 2em;
  margin: 1em 0;
  border-radius: 0.3em;
  background-color: rgb(243, 243, 243);
  
}